html {
  box-sizing: border-box;
  height: 100%;
  font-weight: 400;
  font-family: 'Roboto', 'Arial', sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 375px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: inherit;
}
